.hero__pagination-bullet {
  position: relative;
  width: 15px;
  height: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  cursor: pointer;
}
.hero__pagination-bullet::before {
  width: 10px;
  height: 10px;
  background: transparent;
  border: 1px solid #ffffff;
  border-radius: 50%;
  transition: all 0.2s;
  content: "";
}
.hero__pagination-bullet::after {
  position: absolute;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  margin: auto auto;
  width: 14px;
  height: 14px;
  background: transparent;
  display: none;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  content: "";
}
.hero__pagination-bullet--active::before {
  background: #ffffff;
}
