  .hero__pagination-bullet::before {
    width: 8px;
    height: 8px;
  }
  .hero__pagination-bullet::after {
    width: 12px;
    height: 12px;
  }
}
.hero__pagination-bullet.focus-visible:focus {
  outline: none;
}
.hero__pagination-bullet.focus-visible:focus::after {
  display: block;
}
@media (hover: hover) {
  .hero__pagination-bullet:hover:not(.focus-visible)::before {
    background: #d9d9d9;
  }
}
.hero__pagination-bullet:active::before {
  background: rgba(255, 255, 255, 0.5);
}
.hero__pagination-bullet:active:hover:focus::before {
  background: rgba(255, 255, 255, 0.5);
}

